.button-primary {
  border: 1px solid $yellow;
  background-color: $gray;
  color: $yellow;
  padding: 8px 12px; }

.button-secondary {
  border: none;
  background-color: transparent;
  color: $yellow;
  outline: none;
  text-decoration: underline; }

.button-inline {
  color: $yellow;
  text-decoration: underline;
  font-size: 16px; }
